import { useState, useEffect } from 'react';
import { api } from '../../services/api';
import styles from './products.module.css';

interface Product {
  _id: string;
  vendor: string;
  name: string;
  description?: string;
  price: number;
  category?: string;
  image?: string;
  isAvailable: boolean;
  stock: number;
  createdAt?: string;
}

const emptyProduct: Partial<Product> = {
  name: '',
  description: '',
  price: 0,
  category: '',
  image: '',
  isAvailable: true,
  stock: 0,
};

export default function VendorProducts() {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(false);
  const [mode, setMode] = useState<'list' | 'create' | 'edit'>('list');
  const [editingId, setEditingId] = useState<string | null>(null);
  const [formData, setFormData] = useState<Partial<Product>>(emptyProduct);
  const [vendorId, setVendorId] = useState<string>('');

  useEffect(() => {
    // Get vendor ID from stored user data
    if (typeof window !== 'undefined') {
      try {
        const stored = localStorage.getItem('cc_user');
        if (stored) {
          const parsed = JSON.parse(stored);
          setVendorId(parsed.id);
        }
      } catch {}
    }
  }, []);

  // Load products when vendorId is set
  useEffect(() => {
    if (vendorId) {
      loadProducts();
    }
  }, [vendorId]);

  const loadProducts = async () => {
    try {
      setLoading(true);
      const data = await api.products.getAll();
      // Filter products for current vendor
      const vendorProducts = data.filter((p: Product) => {
        if (!p.vendor) return false;
        // p.vendor can be either a string (ID) or an object (populated)
        const pVendorId = typeof p.vendor === 'object' ? (p.vendor as any)._id : p.vendor;
        return pVendorId === vendorId;
      });
      setProducts(vendorProducts);
    } catch (error) {
      console.error('Error loading products:', error);
      alert('Failed to load products');
    } finally {
      setLoading(false);
    }
  };

  const handleCreate = () => {
    setMode('create');
    setFormData({ ...emptyProduct, vendor: vendorId });
    setEditingId(null);
  };

  const handleEdit = async (id: string) => {
    try {
      const product = await api.products.getById(id);
      setFormData(product);
      setEditingId(id);
      setMode('edit');
    } catch (error) {
      console.error('Error loading product:', error);
      alert('Failed to load product details');
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm('Are you sure you want to delete this product?')) return;
    try {
      await api.products.delete(id);
      alert('Product deleted successfully');
      loadProducts();
    } catch (error) {
      console.error('Error deleting product:', error);
      alert('Failed to delete product');
    }
  };

  const handleToggleAvailability = async (product: Product) => {
    try {
      await api.products.update(product._id, {
        ...product,
        isAvailable: !product.isAvailable,
      });
      loadProducts();
    } catch (error) {
      console.error('Error updating product:', error);
      alert('Failed to update product availability');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      setLoading(true);
      const payload = { ...formData, vendor: vendorId };
      if (mode === 'create') {
        await api.products.create(payload);
        alert('Product created successfully');
      } else {
        await api.products.update(editingId!, payload);
        alert('Product updated successfully');
      }
      setMode('list');
      loadProducts();
    } catch (error) {
      console.error('Error saving product:', error);
      alert('Failed to save product');
    } finally {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    setMode('list');
    setFormData(emptyProduct);
    setEditingId(null);
  };

  const updateField = (field: string, value: any) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  if (mode === 'list') {
    return (
      <>
        <div className={styles.productsHeader}>
          <h1 className={styles.productsTitle}>Inventory Management</h1>
          <button className={styles.btnPrimary} onClick={handleCreate}>
            Add New Product
          </button>
        </div>

        {loading ? (
          <div className={styles.productsLoading}>Loading products...</div>
        ) : (
          <>
            <div className={styles.productsStats}>
              <div className={styles.statCard}>
                <div className={styles.statLabel}>Total Products</div>
                <div className={styles.statValue}>{products.length}</div>
              </div>
              <div className={styles.statCard}>
                <div className={styles.statLabel}>Available</div>
                <div className={styles.statValue}>{products.filter((p) => p.isAvailable).length}</div>
              </div>
              <div className={styles.statCard}>
                <div className={styles.statLabel}>Out of Stock</div>
                <div className={styles.statValue}>{products.filter((p) => p.stock === 0).length}</div>
              </div>
              <div className={styles.statCard}>
                <div className={styles.statLabel}>Low Stock</div>
                <div className={styles.statValue}>{products.filter((p) => p.stock > 0 && p.stock < 10).length}</div>
              </div>
            </div>

            <section className={styles.productsSection}>
              <h2 className={styles.sectionTitle}>Product List</h2>
              <div className={styles.productsTableWrap}>
                <table className={styles.productsTable}>
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Category</th>
                      <th>Price</th>
                      <th>Stock</th>
                      <th>Status</th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {products.map((product) => (
                      <tr key={product._id}>
                        <td>
                          <div className={styles.productNameCell}>
                            {product.image && (
                              <img src={product.image} alt={product.name} className={styles.productThumb} />
                            )}
                            <div>
                              <div className={styles.productName}>{product.name}</div>
                              {product.description && (
                                <div className={styles.productDesc}>{product.description.substring(0, 50)}...</div>
                              )}
                            </div>
                          </div>
                        </td>
                        <td>{product.category || '—'}</td>
                        <td>₹{product.price.toFixed(2)}</td>
                        <td>
                          <span
                            className={`${styles.stockBadge} ${
                              product.stock === 0 ? styles.stockBadgeOutOfStock : product.stock < 10 ? styles.stockBadgeLowStock : ''
                            }`}
                          >
                            {product.stock}
                          </span>
                        </td>
                        <td>
                          <span className={`${styles.statusBadge} ${product.isAvailable ? styles.statusBadgeAvailable : styles.statusBadgeUnavailable}`}>
                            {product.isAvailable ? 'Available' : 'Disabled'}
                          </span>
                        </td>
                        <td>
                          <div className={styles.actionBtns}>
                            <button className={styles.btnEdit} onClick={() => handleEdit(product._id)}>
                              Edit
                            </button>
                            <button
                              className={`${styles.btnToggle} ${product.isAvailable ? styles.disable : styles.enable}`}
                              onClick={() => handleToggleAvailability(product)}
                            >
                              {product.isAvailable ? 'Disable' : 'Enable'}
                            </button>
                            <button className={styles.btnDelete} onClick={() => handleDelete(product._id)}>
                              Delete
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}
                    {products.length === 0 && (
                      <tr>
                        <td colSpan={6} className={styles.emptyState}>
                          No products found. Add your first product to get started!
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </section>
          </>
        )}
      </>
    );
  }

  return (
    <>
      <div className={styles.productsHeader}>
        <h1 className={styles.productsTitle}>{mode === 'create' ? 'Add New Product' : 'Edit Product'}</h1>
        <button className={styles.btnSecondary} onClick={handleCancel}>
          Back to List
        </button>
      </div>

      <form className={styles.formContainer} onSubmit={handleSubmit}>
        <div className={styles.formSection}>
          <h3 className={styles.formSectionTitle}>Product Information</h3>
          <div className={styles.formGrid}>
            <div className={styles.formGroup}>
              <label className={styles.formLabel}>Product Name *</label>
              <input
                type="text"
                required
                className={styles.formInput}
                value={formData.name || ''}
                onChange={(e) => updateField('name', e.target.value)}
              />
            </div>
            <div className={styles.formGroup}>
              <label className={styles.formLabel}>Category</label>
              <input
                type="text"
                className={styles.formInput}
                value={formData.category || ''}
                onChange={(e) => updateField('category', e.target.value)}
              />
            </div>
            <div className={styles.formGroup}>
              <label className={styles.formLabel}>Price (₹) *</label>
              <input
                type="number"
                required
                min="0"
                step="0.01"
                className={styles.formInput}
                value={formData.price || ''}
                onChange={(e) => updateField('price', Number(e.target.value))}
              />
            </div>
            <div className={styles.formGroup}>
              <label className={styles.formLabel}>Stock Quantity *</label>
              <input
                type="number"
                required
                min="0"
                className={styles.formInput}
                value={formData.stock || ''}
                onChange={(e) => updateField('stock', Number(e.target.value))}
              />
            </div>
            <div className={styles.formGroup}>
              <label className={styles.formLabel}>Description</label>
              <textarea
                rows={3}
                className={styles.formTextarea}
                value={formData.description || ''}
                onChange={(e) => updateField('description', e.target.value)}
              />
            </div>
            <div className={styles.formGroup}>
              <label className={styles.formLabel}>Image URL</label>
              <input
                type="url"
                className={styles.formInput}
                value={formData.image || ''}
                onChange={(e) => updateField('image', e.target.value)}
              />
              {formData.image && (
                <div className={styles.imagePreview}>
                  <img src={formData.image} alt="Preview" />
                </div>
              )}
            </div>
            <div className={styles.formGroup}>
              <label className={styles.toggleInput}>
                <input
                  type="checkbox"
                  checked={formData.isAvailable || false}
                  onChange={(e) => updateField('isAvailable', e.target.checked)}
                />
                Available for Sale
              </label>
            </div>
          </div>
        </div>

        <div className={styles.formActions}>
          <button type="button" className={styles.btnSecondary} onClick={handleCancel}>
            Cancel
          </button>
          <button type="submit" className={styles.btnPrimary} disabled={loading}>
            {loading ? 'Saving...' : mode === 'create' ? 'Add Product' : 'Update Product'}
          </button>
        </div>
      </form>
    </>
  );
}
